import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
// import { QueryVhweb, OrdinaryAlgorithm, FirebaseAuth, OrdinaryComponent } from 'private-angular-vhframeworks';
import { AddAccountComponent } from './add-account/add-account.component';
import { EditAccountComponent } from './edit-account/edit-account.component';

@Component({
  selector: 'app-payment-account',
  templateUrl: './payment-account.component.html',
  styleUrls: ['./payment-account.component.scss']
})
export class PaymentAccountComponent implements OnInit {

  careStaffClass:any =[]
  loading:boolean = false
  constructor(
    // public queryVhweb:QueryVhweb,
    // public ordinaryAlgorithm:OrdinaryAlgorithm,
    public dialog: MatDialog,
    // public firebaseAuth: FirebaseAuth,
    // public ordinaryComponent: OrdinaryComponent,
  ) { }

  ngOnInit(): void {
    this.loading =true
    this.getCareStaffClass()
  }

  getCareStaffClass(){
    // this.queryVhweb.getCareStaffClasss()
    // .then((data)=>{
    //   this.careStaffClass = this.ordinaryAlgorithm.sortbyASC(data,'name') 
    //   this.loading =false
    //   console.log(data)
    // }, err=> {
    //   this.loading =false
    //   console.log(err)
    // })
  }

  openAddClass(){
    const dialogRef = this.dialog.open(AddAccountComponent, {
      width: '35%',
      maxWidth: '100%',
      disableClose:true
    });

    dialogRef.afterClosed().subscribe((result) => {
      if(result) this.getCareStaffClass();
    });
  }
  openEditClass(item){
    const dialogRef = this.dialog.open(EditAccountComponent, {
      width: '35%',
      maxWidth: '100%',
      disableClose:true,
      data:item
    });

    dialogRef.afterClosed().subscribe((result) => {
      if(result) this.getCareStaffClass();
    });
  }
  deleteClass(item){
    // this.queryVhweb.deleteCareStaffClass(item._id)
    // .then((bol)=>{
    //   this.getCareStaffClass()
    //   this.ordinaryComponent.showToast(1500, "Đã xóa thành công")
    // },err=>  this.ordinaryComponent.showToast(1500, "Lỗi, vui lòng thử lại"))
  }

}
