import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AccountantComponent } from './accountant.component';
import { Routes, RouterModule } from '@angular/router';
import { ViewCommissionComponent } from './view-commission/view-commission.component';
import { CreateCommissionComponent } from './create-commission/create-commission.component';
import { HistoryCommissionComponent } from './history-commission/history-commission.component';
import { MaterialModule } from 'src/app/dealer/material.module';
import { ModalAddComponent } from './create-commission/modal-add/modal-add.component';
import { ModalEditComponent } from './create-commission/modal-edit/modal-edit.component';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { ShowDetailCommissionComponent } from './view-commission/show-detail-commission/show-detail-commission.component';
import { PaymentToEnduserComponent } from './payment-to-enduser/payment-to-enduser.component';
import { PaymentAccountComponent } from './payment-account/payment-account.component';
import { AddBranchEnduserComponent } from './add-branch-enduser/add-branch-enduser.component';
import { AddBranchComponent } from './add-branch-enduser/add-branch/add-branch.component';
import { AddAccountComponent } from './payment-account/add-account/add-account.component';
import { EditAccountComponent } from './payment-account/edit-account/edit-account.component';

const routes: Routes = [
  {
    path: 'view',
    component: ViewCommissionComponent,
  },
  {
    path: 'create',
    component: CreateCommissionComponent,
  },
  {
    path: 'history',
    component: HistoryCommissionComponent,
  },
  {
    path: 'add-branch-enduser',
    component: AddBranchEnduserComponent,
  },
  {
    path: 'payment-to-enduser',
    component: PaymentToEnduserComponent,
  },
  {
    path: 'payment-account',
    component: PaymentAccountComponent,
  },
];

@NgModule({
  declarations: [
    AccountantComponent,
    ViewCommissionComponent,
    CreateCommissionComponent,
    HistoryCommissionComponent,
    ModalAddComponent,
    ModalEditComponent,
    ShowDetailCommissionComponent,
    PaymentToEnduserComponent,
    PaymentAccountComponent,
    AddBranchEnduserComponent,
    AddBranchComponent,
    AddAccountComponent,
    EditAccountComponent,
  ],
  imports: [
    CommonModule,
    MaterialModule,
    ReactiveFormsModule,
    FormsModule,
    RouterModule.forChild(routes),
  ],
})
export class AccountantModule {}
